.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  font-size: 16px;
  background-color: rgb(112, 193, 211);
  color: black;
  font-family: Helvetica, Arial, sans-serif;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/*Images*/
.body{
  background-image: url(https://thumbs.dreamstime.com/b/nail-salon-interior-as-creative-abstract-blur-background-pedicure-armchairs-modern-inside-beauty-studio-white-blue-140835941.jpg);
  background-size: cover;
}
.one{ 
  float: right;
  margin: 30px 30px;
  background-color: #FAF0F1;
  border: solid black 5px;
  box-shadow: black 7px 7px 7px;
  position: left;
}
.two{
  float: left;
  margin: 30px 30px;
  background-color: #FAF0F1;
  border: solid black 5px;
  box-shadow: black 7px 7px 7px;
}

/*form*/
.form {
  position : relative;
  width  : 740px;
  height : 498px;
  margin : 0 auto;
  padding: 1em;
  box-sizing: border-box;
  background-image: url(https://profunda-analytics.com/wp-content/uploads/2017/07/Contact-Form-background.jpg);
  text-align: left;
}
.form button {
  margin     : 10px 10px;
  font         : bold .6em sans-serif;
  border       : 2px solid #333;
  border-radius: 5px;
  cursor       : pointer;
}
.Reviews{
  position: relative;
  width: 80%;
  margin: 30px 30px;
  border: solid black 3px;
  color: black;
}
/*navbar*/
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

 li {
  float: left;
}

 li a {
  display: block;
  padding: 8px;
  background-color: grey;
}
 a{
  list-style: none;
  text-decoration: none;
}
li a:hover {
  background-color: #BCD8C1;
}
/*iframe*/
.iframe{
  margin: 10px 10px;
  border: solid 5px;
  box-shadow: white 10px 10px 10px;
}
/*text*/

h1, h2, h3{
  text-shadow: 1px 1px 1px white,2px 2px 1px white;
  font-family: 'Times New Roman', Times, serif;
  color: black;
}

p {
  font-size: 1.5rem;
  color: black;
  font-family: Helvetica, Arial, sans-serif;
}
